import React from "react";
import clsx from "clsx";
import { ButtonProps, ButtonType, Variant } from "./Button.types";
import "./Button.scss";

const Button: React.FC<ButtonProps> = ({
  label,
  type,
  variant = Variant.PRIMARY,
  external = false,
  disabled = false,
  iconLeft,
  iconRight,
  href,
  onClick,
}) => {
  const styling = clsx({
    button: true,
    "button--primary": variant === Variant.PRIMARY,
    "button--secondary": variant === Variant.SECONDARY,
    "button--primary button--primary--white": variant === Variant.WHITE_PRIMARY,
    "button--secondary button--secondary--white":
      variant === Variant.WHITE_SECONDARY,
    "button--danger": variant === Variant.DANGER,
  });

  return href != null && href.length ? (
    <a
      target={external ? "_blank" : undefined}
      rel={external ? "noreferrer noopener" : undefined}
      href={href}
      className={styling}
    >
      {/* If passing a relative/absolute path as icon */}
      {iconLeft && <img src={iconLeft} alt="" />}
      {label}
      {/* If passing a relative/absolute path as icon */}
      {iconRight && <img src={iconRight} alt="" />}
    </a>
  ) : (
    <button
      type={type ?? "button"}
      className={styling}
      onClick={onClick}
      disabled={disabled}
    >
      {/* If passing a relative/absolute path as icon */}
      {iconLeft && <img src={iconLeft} alt="" />}

      {label}
      {/* If passing a relative/absolute path as icon */}
      {iconRight && <img src={iconRight} alt="" />}
    </button>
  );
};

Button.defaultProps = {
  label: "Button",
  type: `${ButtonType.BUTTON}`,
  variant: Variant.PRIMARY,
  disabled: false,
  iconLeft: "",
  iconRight: "",
  href: "",
  external: false,
  onClick: () => null,
};

export default Button;
